* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.page {
  color: #504f5e;
  scroll-behavior: smooth;
  font-family: Raleway, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__body {
  background-color: #f1f5f4;
  min-width: 320px;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.page__header {
  margin-bottom: 90px;
}

@media (width >= 768px) {
  .page__header {
    margin-bottom: 100px;
  }
}

@media (width >= 1280px) {
  .page__header {
    margin-bottom: 110px;
  }
}

